import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import {  Card,  } from 'antd';
import { actionCreators } from './store';
import OrderList from '@/pages/account/subpage/exports/components/OrderList';


class Outstanding extends PureComponent {


	componentWillMount(){
		 const  { data } = this.props.teamHistory
		!data.length&&this.props.init()

	}

	render() {
		const {theme} =this.props
		const { data } = this.props.teamHistory
		const historyProps = {
			dataSource:data,
			columns:[
				{
					title : 'Ticket',
					dataIndex:'orderNo',
					key:'orderNo',
				},
				{
					title:'Open Time',
					dataIndex:'openTime',
					key:'openTime',
				},
				{
					title:'Type',
					dataIndex:'type',
					key:'type',
				},
				{
					title:'Size',
					dataIndex:'size',
					key:'size',
				},
				{
					title:'Item',
					dataIndex:'item',
					key:'item',
				},
				{
					title:'Price',
					dataIndex:'price',
					key:'price1',
				},
				{
					title:'Close Time',
					dataIndex:'closeTime',
					key:'closeTime',
				},
				{
					title:'Price',
					dataIndex:'price',
					key:'price2',
				
				},
				{
					title:'Profit',
					dataIndex:'profit',
					key:'profit',
				}
			]
		}
		return (
			<div className="outstanding" >

			
				<Card
				className="history"
					bordered={false}
					title={
						<>
							<h2></h2>
							<p>Best deal in history</p>
						</>
					}
					headStyle={{
						color: '#fff',
						border: 'none'
					}}
				>
						<OrderList type="position" {...historyProps} />
				</Card>
		
		
				<Card
					bordered={false}
					title={
						<>
							<h2></h2>
							<p>Good trading strategy terms</p>
						</>
					}
					className="terms"
					headStyle={{
						color: '#fff',
						border: 'none'
					}}
				>
					<ol  >
						<li>Alph_a </li>
						<li>Alph_b </li>
						<li>Alph_c </li>
						<li>Alph_d </li>
						<li>Alph_e </li>
					</ol>
				</Card>
				</div>
		)
	}
}

const mapStateToProps = state=>({
	teamHistory : state.get('teamHistory').toJS(),
	theme:state.getIn(['header', 'theme'])
})
const mapDispatchToProps = dispatch => ({
  init(){
    dispatch(actionCreators.getData())
  }
})

export  default connect(mapStateToProps,mapDispatchToProps) (Outstanding) ;
